let posicion = window.pageYOffset;
const scrollSuave = document.querySelectorAll('.scroll-suave');
const inicio = document.querySelector('.volver-arriba');
const btnSeguir = document.querySelector('.btn-seguir');

const header = document.querySelector('.header__nav');
const distanciaVer = document.querySelector('#acerca').offsetTop; 

let destino;
let distancia;
let intervalo;

document.addEventListener('DOMContentLoaded', function () {  
    suaveScroll();
    fijarMenu();
})

function fijarMenu(){
    window.addEventListener('scroll', function () {  
        let desplazamiento = window.pageYOffset;
        if(desplazamiento >= distanciaVer -150){
            header.classList.add('fijo');
        }
        else{
            header.classList.remove('fijo');
        }
    })
}

function suaveScroll(){
    scrollSuave.forEach(scroll => {
        scroll.addEventListener('click', moverse);
    });

    btnSeguir.addEventListener('click', bajar);
    inicio.addEventListener('click', subir);

}

function bajar(e){
    e.preventDefault();
    let offsetSeccion = document.querySelector('#acerca').offsetTop -120;
    clearInterval(intervalo);
    posicion = window.pageYOffset;

    intervalo = setInterval(function () {  
        if(posicion < offsetSeccion){
            posicion += 15;
            if(posicion >= offsetSeccion){
                clearInterval(intervalo);
            }
        }

        window.scrollTo(0,  posicion);
    }, 15)
    
}

function moverse(e){
    e.preventDefault();
    destino = e.target.getAttribute('href');
    distancia = document.querySelector(destino).offsetTop - 120;
    clearInterval(intervalo);
    posicion = window.pageYOffset;

    intervalo = setInterval(function () {  
        if(posicion < distancia){
            posicion += 15;
            if(posicion >= distancia){
                clearInterval(intervalo);
            }
        }
        else{
            posicion -= 15;
            if(posicion <= distancia){
                clearInterval(intervalo);
            }
        }

        window.scrollTo(0,  posicion);
    }, 15)

}

function subir(e){
    e.preventDefault();
    posicion = window.pageYOffset;

    intervalo = setInterval(function(){
        if(posicion > 0){
            posicion -= 15;

            if(posicion <= 0){
                clearInterval(intervalo);
            }
        }

        window.scrollTo(0,  posicion); 

    }, 15) 

}

