const btnMenu = document.querySelector('.bx-menu-alt-right');
const btnCerrar = document.querySelector('.bx-x');
const menu = document.querySelector('.nav__enlaces');
const enlacesMenu = document.querySelector('.nav__enlaces a');

mostrarMenu();
cerrarMenu();

function mostrarMenu(){
    btnMenu.addEventListener('click', function() {
        menu.classList.add('abrir');
        btnCerrar.classList.add('mostrar');
        btnMenu.classList.add('ocultar');

        btnCerrar.style.opacity = 0;

        setTimeout(() => {
            btnCerrar.style.opacity = 1;
        }, 300);
    });
}

function cerrarMenu(){
    btnCerrar.addEventListener('click', function() {
        menu.classList.remove('abrir');
        btnCerrar.classList.remove('mostrar');
        btnMenu.classList.remove('ocultar');
    });
}

window.addEventListener('click', e=>{
    if(menu.classList.contains('abrir') && e.target != menu && e.target != btnMenu){
        menu.classList.remove('abrir');
        btnCerrar.classList.remove('mostrar');
        btnMenu.classList.remove('ocultar');
    }
})
